.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) {
  .container {
    min-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  height: 80px;
  color: #fff;
  z-index: 100;
  -webkit-backdrop-filter: blur(6px);
  backdrop-filter: blur(6px);
  background-color: #000;
  padding-top: 20px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header-container {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.header-logo {
  flex-grow: 1;
  font-size: 24px;
  font-style: italic;
  font-weight: 500;
}

@media screen and (min-width: 768px) {
  .header-logo {
    font-size: 32px;
  }
}

.menu {
  flex-direction: row;
  flex-grow: 0;
  gap: 35px;
  font-size: 16px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu {
    font-size: 22px;
  }
}

.menu-list {
  flex-direction: row;
  align-items: center;
  gap: 25px;
}

@media screen and (min-width: 320px) {
  .menu-list {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .menu-list {
    display: flex;
  }
}

.menu-button {
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 15px;
  padding: 10px;
  font-weight: 500;
  box-shadow: 0 0 10px 5px rgba(255, 255, 255, .75);
}

.menu-button:hover {
  color: #000;
  background: #fff;
}

@media screen and (min-width: 768px) {
  .menu-button {
    box-shadow: 0 0 15px 7px rgba(255, 255, 255, .75);
  }
}

.hero {
  margin-top: 80px;
}

.slider-photo {
  width: 100%;
  height: calc(100vh - 80px);
  margin: 0 auto;
  padding: 0 2px;
}

@media screen and (min-width: 768px) {
  .slider-photo {
    padding: 0 5px;
  }
}

.slider-item:hover {
  cursor: pointer;
}

.slick-dots {
  font-size: 0;
  position: absolute;
  bottom: -50px;
  left: 50%;
  transform: rotate(270deg);
}

.slick-dots li {
  margin: 10px 0;
}

.slick-dots li.slick-active button {
  background-color: #000;
}

.slick-dots button {
  width: 10px;
  height: 15px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #000;
  border-radius: 50%;
  outline: 0;
}

.arrow-scroll {
  text-align: center;
  z-index: 9;
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
}

.arrow-scroll__wrap {
  cursor: pointer;
  transition: all .2s ease-in-out;
  display: inline-block;
}

.arrow-scroll__wrap:hover {
  opacity: .5;
}

.arrow-scroll__wrap_animated {
  animation: arrow-scroll 1.7s infinite;
}

@keyframes arrow-scroll {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-7px);
  }

  55% {
    transform: translateY(-7px);
  }

  100% {
    transform: translateY(0);
  }
}

